<template>
<div class="container-fluid join-step-wrapper">
    <div class="row m-0 join-step-row-content">
        <TextInputButton isRequired :wrapperClass="'w-100'" :label="$t('Joining.Email')" :btnLabel="$t('Common.Check')" :action="duplicationIDCheck" :name="'email'" :disabled="confirmSuccess" :placeholder="$t('Joining.placeholder.Email')" v-validate="'required|email'" v-model="FormData.email" />
        <TextInputButton :label="''" :wrapperClass="'w-100'" :btnLabel="$t('Common.Confirm')" :name="'confirmKey'" :action="checkCertEmail" :disabled="confirmSuccess" :placeholder="$t('Joining.placeholder.Number6digits')" v-validate="'required'" v-model="FormData.confirmKey" />
        <TextInput isRequired :label="$t('Joining.Name')" :wrapperClass="'w-100'" :name="'name'" :placeholder="$t('Joining.placeholder.Name')" v-model="FormData.userNm" v-validate="'required'" :maxLength="255" />
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col p-0 cy-col-pad">
            <TextInput isRequired isPassword :label="$t('Joining.Password')" :wrapperClass="'w-100'" :placeholder="$t('Joining.placeholder.Minimum8characters')" :name="'password'" v-model="FormData.password" v-validate="'required|min:8'" />
        </div>
        <div class="col p-0 cy-col-pad">
            <TextInput isRequired isPassword :label="$t('Joining.PasswordConfirm')" :wrapperClass="'w-100'" :placeholder="$t('Joining.placeholder.Minimum8characters')" :name="'passwordConfirm'" v-model="FormData.passwordConfirm" v-validate="'required|min:8'" />
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <PhoneInput isRequired :label="$t('Joining.Phone')" :wrapperClass="'w-100'" :placeholder="$t('Joining.placeholder.Cellphone')" :name="'phoneNum'" v-model="FormData.phoneNum" :defaultCountry="FormData.phoneNum.regionCode" />
    </div>
    <div class="row m-0 join-step-row-content">
        <DateTimeInput :label="$t('Joining.DOB')" :placeholder="$t('Joining.placeholder.Dateofbirth')" v-model="FormData.birth"></DateTimeInput>
    </div>
    <div class="row m-0 join-step-row-content">
        <RadioInput isRequired :label="$t('Joining.Country')" v-model="FormData.country" :options="[{name: $t(`Joining.${$CYconstants.COUNTRIES.KOREA}`), value: $CYconstants.COUNTRIES.KOREA}, {name: $t(`Joining.${$CYconstants.COUNTRIES.OTHER}`), value: $CYconstants.COUNTRIES.OTHER}]" :name="'countrySelector'" :value="$CYconstants.STATUS.OTHER"></RadioInput>
    </div>
    <div class="row m-0 join-step-row-content">
        <TextInput isRequired :label="$t('Joining.Address1')" :wrapperClass="'w-100'" :name="'address1'" :placeholder="$t('Joining.placeholder.Address1')" v-model="FormData.addr" v-validate="'required'" />
    </div>
    <div class="row m-0 join-step-row-content">
        <TextInput isRequired :label="$t('Joining.Address2')" :wrapperClass="'w-100'" :name="'address2'" :placeholder="$t('Joining.placeholder.Address2')" v-model="FormData.addrDetail" v-validate="'required'" />
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.Photo')" />
        </div>
        <div class="col-12 p-0">
            <FileUpload v-model="FormData.photoFile" />
            <Gap :height="'1rem'"></Gap>
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <Button :label="$t('Joining.Next')" :wrapperClass="'w-100'" @click.native="gotoStepTwo"></Button>
    </div>
</div>
</template>

<script>
import TextInputButton from '@/components/Common/Input/TextInputButton';
import TextInput from '@/components/Common/Input/TextInput';
import PhoneInput from '@/components/Common/Input/PhoneInput';
import DateTimeInput from '@/components/Common/Input/DateTimeInput';
import RadioInput from '@/components/Common/Input/RadioInput';
import FileInput from '@/components/Common/Input/FileInput';
import FileUpload from '@/components/Common/Input/FileUpload';
import InputLabel from '@/components/Common/Input/InputLabel';
import Button from '@/components/Common/Button/Button';
import {
    findCountry
} from '@/validator/phoneCode';
import {
    CHECK_DUP,
    CHECK_DUP_SUCCESS,
    CHECK_DUP_FAILED,
    CHECK_CERT,
    CHECK_CERT_SUCCESS,
    CHECK_CERT_FAILED,
} from '@/mutations/Auth.mutation';
import {
    SHOW_POPUP_BUTTON,
    HIDE_POPUP_BUTTON
} from '@/mutations/Common.mutation';

export default {
    name: "StepOne",
    components: {
        TextInputButton,
        TextInput,
        PhoneInput,
        DateTimeInput,
        RadioInput,
        FileInput,
        FileUpload,
        InputLabel,
        Button,
    },
    data: function () {
        return {
            confirmSuccess: false,
            FormData: {
                birth: '',
                userId: '',
                email: '',
                confirmKey: '',
                userNm: '',
                password: '',
                passwordConfirm: '',
                addr: '',
                addrDetail: '',
                country: this.$CYconstants.COUNTRIES.KOREA,
                photoFile: '',
                phoneNum: {
                    country: {
                        dialCode: ''
                    },
                    regionCode: '',
                    number: {
                        national: ''
                    },
                },
            }
        }
    },
    methods: {
        async gotoStepTwo() {
            try {
                let isValid = await this.$validator.validateAll();
                if (!isValid) {
                    let error = this.errors.items[0];
                    return this.$store.dispatch(SHOW_POPUP_BUTTON, error.msg);
                } else {
                    if (this.FormData.password !== this.FormData.passwordConfirm) {
                        throw new Error(this.$t('Joining.Passwordsdonotmatch'))
                    }
                    if (!this.FormData.phoneNum.country || !this.FormData.phoneNum.number || !this.FormData.phoneNum.number.national) {
                        throw new Error(this.$t('Joining.Cellphoneisinvalid'))
                    }
                    if (!this.confirmSuccess) {
                        throw new Error(this.$t('Joining.Youremailwasnotconfirm'))
                    }
                    await this.$router.push({
                        path: '/joining/step-two',
                        params: {
                            ...this.FormData
                        }
                    });
                }
            } catch (err) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, err.message);
            }
        },
        async duplicationIDCheck() {
            try {
                let isValid = await this.$validator.validate('email', this.FormData.email);
                if (!isValid) {
                    let error = this.errors.items[0];
                    this.$store.dispatch(SHOW_POPUP_BUTTON, error.msg);
                } else {
                    await this.$store.dispatch(CHECK_DUP, {
                        userId: this.FormData.email
                    })
                }
            } catch (err) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, err.message);
            }
        },
        async checkCertEmail() {
            try {
                let isValid = await this.$validator.validate('confirmKey', this.FormData.confirmKey);
                if (!isValid) {
                    let error = this.errors.items[0];
                    this.$store.dispatch(SHOW_POPUP_BUTTON, error.msg);
                } else {
                    let res = await this.$store.dispatch(CHECK_CERT, {
                        inputCertEmail: this.FormData.confirmKey,
                        inputUserEmail: this.FormData.email,
                    });

                    if (res) {
                        this.confirmSuccess = true;
                        this.$store.dispatch(SHOW_POPUP_BUTTON, this.$t('Joining.Emailisverifiedsuccessfully'));
                    }
                }
            } catch (err) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, err.message);
            }
        },
    },
    created: function () {
        this.$validator.attach('email', 'required|email');
        let country = findCountry(this.FormData.phoneNum.country.dialCode);
        if (country) {
            this.FormData.phoneNum.regionCode = country.iso2.toUpperCase();
        }
    },
    mounted: function () {}
}
</script>

<style lang="scss" scoped>
.join-step-wrapper {
    .cy-col-pad {
        // margin-left: .75rem;
        // margin-right: .75rem;
        margin: 0
    }
}
</style>
