<template>
<div class="d-flex flex-column text-input-wrapper" :class="wrapperClass">
    <label class="text-input-label">{{label}}<span class="text-danger">{{isRequired ? ' *':''}}</span></label>
    <input :type="`file`" class="file-input" :value="value" @change="changeLocal" :data-filename="filename || defaultFilename">
    <div class="text-danger error">{{error}}</div>
</div>
</template>

<script>
export default {
    name: 'FileInput',
    props: {
        label: String,
        value: String,
        type: String,
        error: String,
        wrapperClass: String,
        isRequired: {
            type: Boolean,
        }
    },
    model: {
        event: 'change'
    },
    data: function () {
        return {
            filename: '',
            defaultFilename: 'File...',
        }
    },
    methods: {
        changeLocal: function (event) {
            this.$emit('change', event.target.value);
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/variables.scss';

.text-input-wrapper {
    .text-input-label {
        font-family: NotoSansKR;
        font-weight: 300;
        font-size: 1.4rem;
        line-height: 2rem;
        color: #7C848D;
        margin-bottom: .7rem;
    }

    .file-input {
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: normal;
        font-size: 1.4rem;
        line-height: 2.0rem;
        color: #4f4f4f;
        outline: none;
        border: none;
        // border-bottom: 0.05rem solid #E0E0E0;
        padding-bottom: .5rem;
        position: relative;

        &:after {
            content: attr(data-filename);
            position: absolute;
            height: 100%;
            width: 100%;
            background-color: white;
            color: $cy-blue;
        }
    }

    .error {
        display: inline-block;
        font-size: 1.2rem;
        line-height: 2.0rem;
        width: 100%;
        margin-bottom: .3rem;
        min-height: 2.0rem;
        position: relative;
    }
}
</style>
