<template>
    <div class="d-flex flex-column text-input-wrapper" :class="wrapperClass">
        <span class="scope-1">
            <span class="scope-2">
                <label class="text-input-label">{{label}}<span class="text-danger">{{isRequired ? ' *':''}}</span></label>
                <input :type="`${isPassword ? 'password' : 'text'}`" class="text-input" :value="value" @change="changeLocal" :name="name" :disabled="disabled || false" :placeholder="placeholder">
            </span>
            <Button primary :label="btnLabel" :wrapperClass="'input-inline-button'" @click.native="doAction" :isDisabled="disabled || false"></Button>
        </span>
        <div class="text-danger error">{{error}}</div>
    </div>
</template>
<script>
import Button from '@/components/Common/Button/Button';
export default {
    name: 'TextInputButon',
    components: {
        Button,
    },
    props: {
        label: String,
        value: String,
        name: String,
        type: String,
        error: String,
        placeholder: {
            type: String,
            default: '',
        },
        disabled: Boolean,
        wrapperClass: String,
        isPassword: {
            type: Boolean,
            default: false
        },
        isRequired: {
            type: Boolean,
        },
        btnLabel: String,
        action: {
            type: Function,
            default: () => {}
        }
    },
    model: {
        event: 'change'
    },
    methods: {
        changeLocal: function (event) {
            this.$emit('change', event.target.value);
        },
        doAction: function () {
            if (!this.disabled) {
                this.action();
            }
        }
    }
}
</script>
<style lang="scss" scoped>
@import '@/assets/styles/variables.scss';

    .text-input-wrapper {
        .text-input-label {
            font-family: NotoSansKR;
            font-weight: 300;
            font-size: 1.4rem;
            line-height: 2rem;
            color: #7C848D;
            margin-bottom: .4rem;
        }
        .text-input {
            font-family: NotoSansKR;
            font-style: normal;
            font-weight: normal;
            font-size: 1.4rem;
            line-height: 2.0rem;
            color: #4f4f4f;
            outline: none;
            border: none;
            border-bottom: 0.05rem solid #E0E0E0;
            padding-bottom: .5rem;
            &:focus {
                color: $cy-blue;
                border-bottom: 0.05rem solid $cy-blue;
            }

            &::placeholder {
                font-family: NotoSansKR;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 20px;
                color: #4F4F4F;
                opacity: 0.5;
            }
        }
        .error {
            display: inline-block;
            font-size: 1.2rem;
            line-height: 2.0rem;
            width: 100%;
            margin-bottom: .3rem;
            min-height: 2.0rem;
            position: relative;
        }
        .scope-1 {
            display: flex;
            flex-wrap: nowrap;
            .scope-2 {
                display: flex;
                flex-flow: column;
                flex: 1;
                margin-right: 1.5rem;
            }
            .input-inline-button {
                max-height: 3.0rem;
                max-width: 8rem;
                font-family: NotoSansKR;
                font-style: normal;
                font-weight: normal;
                font-size: 1.3rem;
                line-height: 1.9rem;
                display: flex;
                align-items: center;
                text-align: center;
                background: #FFFFFF;
                margin-top: auto;
            }
        }
    }
</style>