<template>
<div class="mobile-page join-step-container">
    <MainLayout :title="`Joining`" :leftIcon="`LEFT_ARROW`" :leftAction="goBack">
        <StepOne></StepOne>
    </MainLayout>
</div>
</template>

<script>
import MainLayout from '@/components/Common/Layout/MainLayout';
import StepOne from '@/components/Joining/StepOne';
import {
    SHOW_POPUP,
    HIDE_POPUP
} from '@/mutations/Common.mutation';

export default {
    name: 'JoinStepOne',
    components: {
        MainLayout,
        StepOne
    },
    methods: {
        goBack: function () {
            this.$store.dispatch(SHOW_POPUP, {
                content: this.$t('Join.Areyousureyouwanttoleavethisscreen'),
                actions: [{
                    name: this.$t('Common.YES'),
                    isDefault: false,
                    callback: () => {
						this.$store.dispatch(HIDE_POPUP);
						this.$router.go(-1);
					},
                    order: 1
                },{
                    name: this.$t('Common.NO'),
                    isDefault: true,
                    callback: () => this.$store.dispatch(HIDE_POPUP),
                    order: 2
                }]
            })
        }
    }
}
</script>

<style lang="scss">
@import '@/assets/styles/variables.scss';
@import '@/assets/styles/join-step.scss';
</style>
